
.statistics {
  width: 55%;
  padding: 35px;
  text-align: center;
  background-color: rgb(230, 238, 247);
  border: 1px solid rgb(141, 137, 137, 0.5);
}

.title {
  padding: 20px 0;
  margin: 0;
  text-transform: uppercase;
  background-color: #fff;
  border: 1px solid rgb(141, 137, 137, 0.5);
}

.statList {
  display: flex;
  width: 100%;
  margin: 0;
  padding: 0;
  color: white;
}

