.profile {
  color: #8f8a8a;
  text-align: center;
  border: 1px solid rgb(232, 236, 242);
  border-radius: 3px;
  width: 400px;
  box-shadow: dimgrey;
  margin: 10px;
}

.avatar {
  height: 159px;
  margin: 46px 0 16px;
  border-radius: 100px;
}

.name {
  font-size: 20px;
  font-weight: 600;
  color: darkslategray;
}
